@import url(./vendor/swiper.min.css);
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-dark: #151515;
  --quill-gray: #e5e5e3;
  --color-gray: #858585;
  --color-tundora: #454545;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-Bold.woff2") format("woff2"), url("../Jost-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-Medium.woff2") format("woff2"), url("../Jost-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

