html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 40px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  font-size: 15px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  opacity: 0;
  transition: opacity 0.15s;
}

.main-link--white {
  color: var(--color-white);
}

.main-link:hover::before {
  opacity: 1;
  transition: opacity 0.15s;
}

.container-narrow {
  max-width: 1406px;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

.header__shop-nav {
  margin-left: 250px;
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.logo svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-dark);
  object-fit: cover;
}

.catalog-header {
  padding-top: 38px;
}

.free-delivery {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 120;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40px;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-white);
  background-color: #111;
}

.free-delivery__btn {
  position: absolute;
  right: 40px;
  top: 50%;
  width: 12px;
  height: 12px;
  transform: translateY(-50%);
}

.free-delivery__btn svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-white);
}

.card-page .header {
  padding-top: 0;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 45px;
}

.nav__link--current::before {
  opacity: 1;
  transition: opacity 0.15s;
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__item:not(:last-child) {
  margin-right: 25px;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.shop-nav__link--user {
  background-image: url("../img/user.png");
}

.cart {
  width: auto;
  height: auto;
}

.cart__icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../img/cart.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.search-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../img/search.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.quantity {
  display: inline-flex;
  align-items: center;
  border-radius: 25px;
  padding: 0 5px;
  height: 16px;
  font-weight: 700;
  font-size: 10px;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.hero {
  position: relative;
  height: 100vh;
  height: 1080px;
  min-height: 700px;
  background-color: var(--quill-gray);
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(var(--color-black), 0.05);
}

.hero__container {
  position: relative;
  height: 100%;
}

.hero__title {
  margin: 0;
  margin-bottom: 40px;
  max-width: 560px;
  font-weight: 700;
  font-size: 72px;
}

.hero__descr {
  margin: 0;
  font-size: 18px;
}

.hero__image {
  position: absolute;
  right: 273px;
  bottom: 0;
  max-height: 90%;
}

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 260px;
  height: 100%;
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.banner-pag {
  left: 50%;
  bottom: 46px;
  transform: translateX(-50%);
}

.banner-pag .swiper-pagination-bullet {
  margin: 0 12px;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.banner-pag .swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  opacity: 1;
}

.banner-pag .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: -1;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background-color: var(--color-white);
  transform: translate(-50%, -50%);
}

.marketing {
  position: absolute;
  left: 50px;
  bottom: 50px;
  z-index: 10;
  display: flex;
  padding: 10px;
  width: 280px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transform: translateX(-150vw);
  transition: transform 0.3s;
}

.marketing--visible {
  transform: translateX(0);
  transition: transform 0.3s;
}

.marketing__text {
  margin-left: 10px;
}

.marketing__image {
  flex-shrink: 0;
}

.marketing__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.marketing__descr {
  display: block;
  margin-bottom: 5px;
  font-size: 11px;
  color: var(--color-gray);
}

.marketing__title {
  margin: 0;
  margin-bottom: 7px;
  padding-right: 20px;
  min-height: 34px;
  font-size: 15px;
}

.marketing__when-from {
  display: block;
  padding-right: 40px;
  font-size: 11px;
  color: var(--color-gray);
}

.marketing__close {
  position: absolute;
  right: 10px;
  top: 10px;
  padding: 0;
  width: 10px;
  height: 10px;
  background-image: url("../img/close.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.banners__container {
  padding-top: 50px;
  padding-bottom: 50px;
}

.banners__list {
  display: flex;
}

.banners__item {
  flex-shrink: 0;
  width: 33%;
}

.banners__item:not(:last-child) {
  margin-right: 0.6%;
}

.banners-article {
  padding: 54px 60px;
  padding-right: 45%;
  min-height: 290px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #e9e4df;
}

.banners-article--midseason {
  background-image: url("../img/midseason.jpg");
}

.banners-article--summer {
  background-image: url("../img/summer.jpg");
}

.banners-article--accs {
  background-image: url("../img/accs.jpg");
}

.banners-article__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 36px;
}

.main-products__container {
  padding-top: 55px;
  padding-bottom: 55px;
}

.main-products__nav {
  display: flex;
  justify-content: center;
  margin-bottom: 46px;
}

.main-products__item {
  margin: 0 18px;
}

.main-products__btn-center {
  text-align: center;
}

.main-products__more {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  margin-top: 95px;
  font-weight: 500;
  font-size: 13px;
  color: var(--color-gray);
}

.main-products__more span {
  display: inline-block;
  margin-bottom: 12px;
}

.main-products__more svg {
  width: 12px;
  height: 21px;
  fill: var(--color-dark);
}

.main-products__btn {
  font-size: 24px;
}

.main-products__btn--current::before {
  opacity: 1;
  transition: opacity 0.15s;
}

.products-grid {
  display: flex;
  flex-wrap: wrap;
}

.products-grid__item {
  margin-right: 30px;
  margin-bottom: 80px;
}

.products-grid__item:nth-child(5n) {
  margin-right: 0;
}

.products-grid__item:nth-last-child(-n+5) {
  margin-bottom: 0;
}

.product {
  position: relative;
}

.product__image {
  position: relative;
  margin-bottom: 20px;
}

.product__image img {
  max-height: 100%;
  object-fit: cover;
}

.product__sale {
  position: absolute;
  left: 20px;
  top: 20px;
  border: 2px solid var(--color-dark);
  padding: 4px 10px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: #111;
}

.product__prop {
  display: inline-block;
  margin-bottom: 10px;
  padding-left: 16px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: #111;
  background-position: left center;
  background-repeat: no-repeat;
}

.product__title {
  margin: 0;
  margin-bottom: 8px;
  font-size: 13px;
  color: var(--color-tundora);
}

.product__title a::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.product__price {
  font-weight: 700;
  font-size: 13px;
}

.product__oldprice {
  margin-left: 5px;
  font-weight: 700;
  font-size: 13px;
  text-decoration: line-through;
  color: #959595;
}

.new {
  background-image: url("../img/new.png");
}

.best {
  background-image: url("../img/best.png");
}

.shop-banners {
  padding-top: 45px;
}

.shop-banners__container {
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.shop-banner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
  width: 50%;
  min-height: 420px;
  color: var(--color-white);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-dark);
}

.shop-banner::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(215, 8, 8, 0.1);
}

.shop-banner__title {
  position: relative;
  z-index: 1;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 48px;
}

.shop-banner__text {
  position: relative;
  z-index: 1;
}

.shop-banner--men {
  background-image: url("../img/banner-1.jpg");
}

.shop-banner--women {
  background-image: url("../img/banner-2.jpg");
}

.hero-catalog {
  padding-top: 130px;
}

.hero-catalog__slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  background-color: #f8f8f8;
}

.hero-catalog__container {
  position: relative;
}

.catalog-slide {
  padding: 108px;
}

.catalog-slide__title {
  margin: 0;
  margin-bottom: 15px;
  max-width: 375px;
  font-weight: 700;
  font-size: 36px;
}

.catalog-slide__descr {
  margin: 0;
  margin-bottom: 40px;
  max-width: 390px;
  font-size: 15px;
  line-height: 28px;
  color: var(--color-tundora);
}

.catalog-slide__btns {
  display: flex;
  align-items: center;
}

.catalog-slide__image {
  position: absolute;
  right: 19%;
  bottom: 0;
}

.catalog-slide__link {
  display: inline-block;
  padding: 16px 43px;
  font-size: 13px;
}

.catalog-slide__link--dark {
  border: 1px solid var(--color-dark);
  color: var(--color-white);
  background-color: var(--color-dark);
}

.catalog-slide__link--light {
  border: 1px solid var(--color-dark);
  color: var(--color-dark);
  background-color: transparent;
}

.catalog-slide__link:not(:last-child) {
  margin-right: 15px;
}

.hero-prev-btn,
.hero-next-btn {
  position: absolute;
  left: 30px;
  top: 50%;
  z-index: 20;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid var(--color-dark);
  width: 48px;
  height: 48px;
  color: var(--color-dark);
  transform: translateY(-50%);
  transition: background-color 0.3s;
}

.hero-prev-btn svg,
.hero-next-btn svg {
  width: 40px;
  height: 20px;
  fill: currentColor;
  transition: fill 0.3s;
}

.hero-prev-btn:hover,
.hero-next-btn:hover {
  background-color: var(--color-dark);
  transition: background-color 0.3s;
}

.hero-prev-btn:hover svg,
.hero-next-btn:hover svg {
  fill: var(--color-white);
  transition: fill 0.3s;
}

.hero-next-btn {
  left: auto;
  right: 30px;
}

.breadcrumbs__list {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
  padding-top: 36px;
  padding-bottom: 26px;
}

.breadcrumbs__item {
  position: relative;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 35px;
}

.breadcrumbs__item:not(:last-child)::after {
  content: "/";
  position: absolute;
  right: -20px;
  top: 50%;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-gray);
  transform: translateY(-50%);
}

.breadcrumbs__link {
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-gray);
}

.breadcrumbs__link--current {
  color: var(--color-dark);
}

.breadcrumbs .card-prev,
.breadcrumbs .card-next {
