.catalog-filters {
  padding-top: 27px;
  padding-right: 62px;
  width: 309px;
  margin-right: 30px;
}

.catalog-content {
  min-height: 500px;
}

.catalog-content .container {
  display: flex;
}

.hide-filters {
  margin-bottom: 18px;
  padding-left: 24px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  background-image: url("../img/cross.png");
  background-position: left center;
  background-repeat: no-repeat;
}

.catalog-filter {
  border-top: 1px solid #eee;
}

.catalog-filter__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 34px;
  padding-bottom: 34px;
  cursor: pointer;
  user-select: none;
}

.catalog-filter__caption {
  display: flex;
  align-items: center;
}

.catalog-filter__title {
  display: inline-block;
  margin: 0;
  font-weight: 700;
  font-size: 13px;
  text-transform: uppercase;
}

.catalog-filter__quantity {
  margin-left: 5px;
  min-width: 16px;
  transform: translateY(-1px);
}

.catalog-filter__bottom {
  display: none;
  padding-bottom: 34px;
}

.catalog-filter__toggle {
  position: relative;
  width: 12px;
  height: 12px;
  color: #aaa;
}

.catalog-filter__toggle::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  width: 2px;
  height: 100%;
  background-color: currentColor;
  transform: translateX(-50%);
}

.catalog-filter__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  transform: translateY(-50%);
}

.catalog-filter__items {
  columns: 2;
}

.catalog-filter__item {
  margin-bottom: 14px;
}

.catalog-filter__item:nth-child(5n) {
  margin-bottom: 0;
}

.catalog-filter--open .catalog-filter__bottom {
  display: block;
}

.catalog-filter--open .catalog-filter__toggle::before {
  display: none;
}

.catalog-filter:last-child {
  border-bottom: 1px solid #eee;
}

.custom-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;
}

.custom-checkbox__text {
  position: relative;
  padding-left: 15px;
  font-size: 13px;
  color: var(--color-gray);
}

.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  border: 2px solid #aaa;
  width: 11px;
  height: 11px;
}

.custom-checkbox__text::after {
  content: "\2713";
  position: absolute;
  left: 2px;
  top: 0;
  color: #aaa;
}

.custom-checkbox__input:checked + span {
  color: var(--color-dark);
}

.custom-checkbox__input:checked + span::before {
  border-color: var(--color-dark);
}

.custom-checkbox__input:checked + span::after {
  color: var(--color-dark);
}

.catalog-grid {
  padding-top: 34px;
  width: 987px;
}

.catalog-grid__content {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 70px;
}

.catalog-grid__item {
  margin-bottom: 79px;
}

[data-grid-columns="3"] .catalog-grid__item {
  width: 31.3%;
}

[data-grid-columns="3"] .catalog-grid__item:not(:nth-child(3n)) {
  margin-right: 3%;
}

[data-grid-columns="3"] .catalog-grid__item:nth-last-child(-n+3) {
  margin-bottom: 0;
}

[data-grid-columns="4"] .catalog-grid__item {
  width: 22.7%;
}

[data-grid-columns="4"] .catalog-grid__item:not(:nth-child(4n)) {
  margin-right: 3%;
}

[data-grid-columns="4"] .catalog-grid__item:nth-last-child(-n+4) {
  margin-bottom: 0;
}

[data-grid-columns="5"] .catalog-grid__item {
  width: 17.6%;
}

[data-grid-columns="5"] .catalog-grid__item:not(:nth-child(5n)) {
  margin-right: 3%;
}

[data-grid-columns="5"] .catalog-grid__item:nth-last-child(-n+5) {
  margin-bottom: 0;
}

.catalog-props {
  margin-bottom: 15px;
}

.catalog-props__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 26px;
}

.catalog-columns {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
}

.catalog-columns__text {
  margin-right: 3px;
}

.catalog-columns__list {
  display: flex;
  align-items: center;
}

.catalog-columns__item:not(:last-child) {
  margin-right: 5px;
}

.catalog-columns__btn {
  padding: 0 5px;
  font-size: 10px;
  color: var(--color-gray);
}

.catalog-columns__btn:hover {
  color: var(--color-dark);
}

.catalog-columns__btn--current {
  color: var(--color-dark);
}

.catalog-columns__btn--current::before {
  opacity: 1;
}

.catalog-choice {
  display: none;
  font-size: 0;
}

.catalog-choice__item {
  margin-right: 5px;
  margin-bottom: 5px;
  border-radius: 12px;
  padding: 5px 12px;
  font-size: 13px;
  color: var(--color-tundora);
  background-color: #eee;
  transition: background-color 0.3s, color 0.3s;
}

.catalog-choice__item svg {
  margin-left: 5px;
  width: 8px;
  height: 9px;
  fill: currentColor;
  pointer-events: none;
}

.catalog-choice__item:hover {
  color: var(--color-white);
  background-color: var(--color-dark);
  transition: background-color 0.3s, color 0.3s;
}

.catalog-choice__clear {
  position: relative;
  font-weight: 500;
  font-size: 13px;
}

.catalog-choice__clear::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  opacity: 1;
  transition: opacity 0.15s;
}

.custom-select {
  position: relative;
  min-width: 115px;
}

.custom-select__dropdown {
  display: none;
  position: absolute;
  left: 0;
  top: calc(100% + 2px);
  z-index: 20;
  padding: 10px;
  width: 100%;
  background-color: var(--color-white);
}

.custom-select__item {
  cursor: pointer;
  transition: color 0.3s;
}

.custom-select__item:not(:last-child) {
  margin-bottom: 10px;
}

.custom-select__item:hover {
  color: var(--color-tundora);
  transition: color 0.3s;
}

.custom-select__top {
  position: relative;
  border-bottom: 1px solid #e5e5e7;
  padding-right: 32px;
  padding-bottom: 12px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  cursor: pointer;
}

.custom-select__top::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -6px;
  width: 10px;
  height: 6px;
  background-image: url("../img/angle-down.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transform: translateY(-50%);
}

.custom-select--open .custom-select__top::after {
  transform: translateY(-50%) rotate(180deg);
}

.custom-select--open .custom-select__dropdown {
  display: block;
}

.custom-select--footer {
  min-width: auto;
}

.custom-select--footer .custom-select__top {
  border-bottom: none;
  padding-right: 15px;
  padding-bottom: 0;
  font-size: 13px;
}

.custom-select--footer .custom-select__top::after {
  margin-top: 0;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination__item:not(:last-child) {
  margin-right: 8px;
}

.pagination__link {
  display: inline-flex;
  align-items: center;
  border: 2px solid #eee;
  padding: 0 16px;
  height: 40px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
  color: var(--color-gray);
  transition: color 0.3s, background-color 0.3s, border 0.3s;
}

.pagination__link svg {
  margin-left: 3px;
  width: 7px;
  height: 16px;
  fill: currentColor;
}

.pagination__link:hover {
  border: 2px solid var(--color-dark);
  color: var(--color-white);
  background-color: var(--color-dark);
  transition: color 0.3s, background-color 0.3s, border 0.3s;
}

.pagination__link--current {
  border: 2px solid var(--color-dark);
  color: var(--color-white);
  background-color: var(--color-dark);
}

